@import url("https://netdna.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
.fa {
  font-family: fontawesome !important;
}
.main_block{
  background-image: url(./img/background-main.svg);
  height: 100vh;
  background-size: cover;
  min-height: 645px;
}
.main_container{
  position: relative;
  padding: 35px 45px 45px;
  height: 100%;
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation{
  display: flex;
  align-items: center;
  width: 770px;
  justify-content: space-between;
}
.nav{
  display: flex;
  list-style: none;
  width: 550px;
  justify-content: space-between;
  padding: 0;
}
.nav li a{
  text-decoration: none;
  color: white;
  padding: 8px 0;
  transition: .7s ease;
  opacity: 0.3;
  font-size: 15px;
  border-bottom: 1px solid transparent;
  font-family: Montserrat,sans-serif;
  font-weight: 500;
}
.nav li:hover a{
  border-bottom: 1px solid white;
  opacity: 1;
}
.search_button{
  border: none;
  background-color: transparent;
}
i.fa{
  font-size: 15px;
  color: white
}
.main-content{
  height: calc(100% - 10%);
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  position: relative;
  padding-left: 120px;
  font-family: Montserrat,sans-serif;
}
.number_block_main{
  font-size: 20px;
  font-weight: 700;
  margin-top: -8%;
}
.main_title{
  font-size: 50px;
  margin-top: 45px;
  margin-bottom: 30px;
  max-width: 485px;
}
.subtitle{
  width: 440px;
  margin: 0;
  font-size: 15px;
  font-weight: 400;
}
.instagram{
  position: absolute;
  left: -45px;
  bottom: 0px;
}
.instagram:hover .instagram_link{
  border-bottom: 1px solid white;
}
.instagram_link{
  transition: .5s ease;
  border-color: transparent;
  padding-bottom: 8px;
  text-decoration: none;
  color: white;
}
.fa-instagram{
  margin-right: 10px;
}
.line{
  position: absolute;
  right: -135px;
  top: 38%;
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
}
.line_span:after{
  display: block;
  width: 90px;
  height: 1px;
  background-color: white;
  bottom: 0;
  top: 0;
  margin: auto;
  position: absolute;
  right: -110px;
  content: "";
}
.main-block-navigation{
  width: 660px;
  display: flex;
  position: absolute;
  right: 0;
  z-index: 99;
  height: 210px;
  bottom: -55px;
}
.main-block-navigation_item{
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  transition: 1s all ease;
}
.main-block-navigation_item a{
  padding: 0 55px;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: white;
  width: 100%;
  transition: 1s all ease;
  height: 100%;
  justify-content: center;
}
.main-block-navigation_item:hover{
  background-color: white;
  transition: 1s all ease;
}
.main-block-navigation_item:hover  a{
  color: black;
  transition: 1s all ease;
}
.main-block-navigation_item-white{
  background-color: white;
}
.main-block-navigation_item-white a{
  color: black
}
.navigation-text{
  font-size: 11px;
  color: #464646;
}
.navigation-number{
  margin: 0;
  font-size: 15px;
  font-family: Montserrat,sans-serif;
  font-weight: 800;
}
.navigation-link-name{
  margin: 15px 0;
  font-family: Montserrat,sans-serif;
  font-weight: 800;
  font-size: 23px;
}