@import url(https://netdna.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    font-family: Montserrat,sans-serif;
}
.container{
    width: 1280px;
    margin: 0 auto;
    position: relative;
}
.menu_column{
    position: fixed;
    top: 0;
    left: calc((100% - 1280px) / 2 + 15px );
    bottom: 0;
    margin: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 9999;
    justify-content: center;
    list-style: none;
}
.column_link{
    color: #979797;
    text-decoration: none;
    font-size: 12px;
    font-weight: 500;
    padding: 5px 0;
    display: block;
}
.column_link.active{
    color: #010101;
    position: relative;
}
.menu_column li:nth-of-type(1) .active{
    color: white;
}
.menu_column li:nth-of-type(1) .active:after{
    background-color: white;
}
.column_link:after{
    height: 0px;
    margin: 0 auto;
    content: "";
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    width: 1px;
    background-color: #949C9C;
    display: block;
}
.active.column_link:after{
    height: 80px;
    margin: 20px auto;
    content: "";
    width: 1px;
    background-color: #949C9C;
    display: block;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
